import React from 'react'
import {List,InputItem, NavBar,Icon,Grid,Toast} from 'antd-mobile'
// import io from 'socket.io-client'
import {connect} from 'react-redux'

import {getMsgList,sendMsg,recvMsg,readMsg} from '../../redux/chat.redux'
import {getChatId} from '../../util'
import QueueAnim from 'rc-queue-anim'

// const socket = io('ws://localhost:9093') redux

@connect(
  state=>state,
  {getMsgList,sendMsg,recvMsg,readMsg}
)
class Chat extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      text:'',
      showEmoji: false
    }
    this.handleSumbit = this.handleSumbit.bind(this)
  }
  componentDidMount(){   // 
    if(!this.props.chat.chatmsg.length) {
      this.props.getMsgList()    // {id:{name,avatar}}
      this.props.recvMsg()       // 
    }
    this.fixCarouse()
  }
  // 
  componentWillUnmount(){
    // id
    const to = this.props.match.params.user
    console.log(to)
    this.props.readMsg(to)
  }
  fixCarouse(){   // gridbug
    setTimeout(function () {   // emoji
      window.dispatchEvent(new Event('resize'))
    },0)
  }
  handleSumbit(){
    console.log('hahahaha')
    //socket.emit('sendmsg',{text: this.state.text})
    const from = this.props.user._id
    const to = this.props.match.params.user
    const msg = this.state.text
    // console.log(msg);
    if(msg.trim()){
      this.props.sendMsg({from,to,msg})
      this.setState({text:''})
    } else {
      Toast.fail('', 2)
    }
  }
  render(){
    const emoji = '                                         '.split(' ').filter(v=>v).map(v=>({
      text: v
    }))
    // id
    const userid = this.props.match.params.user
    // console.log(this.props.match.params.user)
    // connectstate=>statechat.usersstate=>state.chat,this.props.users
    // users{id:{name,avatar}}
    const users = this.props.chat.users
    // console.log('users',users)
    if(!users[userid]) return null      // idnull
    const chatid = getChatId(userid, this.props.user._id)   // id
    // chatmsgfromto
    const chatmsgs = this.props.chat.chatmsg.filter(v=>v.chatid===chatid)
    // console.log('chatmsgs',chatmsgs)
    return (
      <div id='chat-page'>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={() => {
            this.props.history.goBack()
          }}
        >
          {users[userid].name}
        </NavBar>
        <QueueAnim delay={100}>
        {chatmsgs.map(v=>{
          // 
          const avatar = require(`../img/${users[v.from].avatar}.jpg`)
          // 
          return v.from === userid ? (
          <List key={v._id}>
            <List.Item
              thumb={avatar}>
              {v.content}
            </List.Item>
          </List>
          ) : (
            <List key={v._id+'0'}>
              <List.Item className="chat-me" extra={<img src={avatar} alt="" />}>
                {v.content}
              </List.Item>
            </List>
          )
        })}
        </QueueAnim>
        <div className="stick-footer">
          <List>
            <InputItem
              placeholder=""
              value={this.state.text}
              onChange={v=>{
                this.setState({text:v})
              }}
              extra={
                <div>
                  <span style={{marginRight:15}} onClick={()=>{
                    this.setState({showEmoji:!this.state.showEmoji})
                    this.fixCarouse()
                  }} role="img" aria-label="send emoji"></span>
                  <span onClick={this.handleSumbit}>gg</span>
                </div>
              }
            ></InputItem>
          </List>
          {this.state.showEmoji
            ? <Grid
            data={emoji}
            columnNum={9}
            carouselMaxRow={4}
            isCarousel={true}
            onClick={el=>{
              this.setState({
                text: this.state.text + el.text
              })
            }
            }
          /> : null}
        </div>
      </div>
    )
  }
}

export default Chat



